<template>
  <div
    class="rail"
    data-rail="two"
    ref="two"
    @mousedown="getDragPointer($event)"
    v-touch:tap="touchTab"
    v-touch:moving="touchMove"
  ></div>
</template>

<script>
import { mapState } from "vuex";
import * as helpers from "@/helpers/home";

export default {
  name: "RightRail",
  data() {
    return {
      rail: null,
      shiftX: null,
      screenwidth: window.innerWidth,
    };
  },
  computed: {
    ...mapState(["two"]),
  },
  mounted() {
    this.rail = this.$refs.two;
  },
  methods: {
    touchTab(e) {
      if (e.changedTouches) {
        const x = e.changedTouches[0].clientX;
        const elRect = e.target.getBoundingClientRect();
        this.shiftX = x - elRect.left;
      }
    },
    touchMove(e) {
      if (e.changedTouches) {
        helpers.moveRail(
          this.shiftX,
          this.rail,
          e.changedTouches[0].pageX,
          "two"
        );
      }
    },
    getDragPointer(e) {
      const x = e.clientX;
      const elRect = e.target.getBoundingClientRect();
      this.shiftX = x - elRect.left;
      document.addEventListener("mousemove", this.onMouseMove);
      document.addEventListener("mouseup", this.onMouseUp);
    },

    onMouseMove(e) {
      e.preventDefault();
      helpers.moveRail(this.shiftX, this.rail, e.pageX, "two");
    },
    onMouseUp() {
      document.getElementsByTagName("body")[0].style.cursor = "";
      document.removeEventListener("mousemove", this.onMouseMove);
    },
  },
};
</script>

